
.profile {
  margin: 40px 0 0 0;
  
  display: grid;
  grid-template-columns: 120px 336px 1fr;
  column-gap: 30px;
  align-items: center;
}

@media screen and (max-width: 767px) {
  .profile {
    width: 282px;
    margin: 42px auto 0 auto;
    grid-template-columns: 1fr;
    grid-template-rows: repeat(3, auto);
    row-gap: 36px;
    justify-items: center;
  }
}